:root {
  scroll-behavior: auto !important;
}

//cursor hover effect
.wcf-hover-cursor-effect {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  top: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  background: #000;
  color: #fff;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  z-index: 999;
}

//image hover effect
.wcf-image-hover {
  transition: opacity 0.3s, transform 0.7s cubic-bezier(0.23, 1, 0.32, 1);
  position: absolute;
  width: 300px;
  height: 400px;
  object-fit: cover;
  left: 0;
  top: -50%;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  pointer-events: none;
}

[data-wcf-wrapper-link] {
  cursor: pointer
}

//cursor
.wcf-cursor {
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  border: 1px solid #000;
  z-index: 999;
  transition: 0.15s;
}

.wcf-cursor-follower {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  pointer-events: none;
  width: 8px;
  height: 8px;
  background-color: #000;
  border-radius: 50%;
  transition: 0.2s;
  z-index: 999;
}

//scroll to top
.wcf-scroll-to-top {
  svg {
    width: 1em;
    height: 1em;
  }

  position: fixed;
  right: 30px;
  bottom: 30px;
  height: 46px;
  width: 46px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  transform: translateY(15px);
  transition: all 200ms linear;

  &.show-scroll-to-top {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
  }

  &.scroll-to-circle {
    border-radius: 100%;
  }

  svg.progress-circle {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;

    path {
      fill: none;
      stroke: #1f2029;
      stroke-width: 4;
      box-sizing: border-box;
      -webkit-transition: all 200ms linear;
      transition: all 200ms linear;
    }
  }
}

//scroll indicator
.wcf-scroll-indicator {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 8px;
  background: #ccc;

  .indicator-bar {
    height: 100%;
    background: #000000;
    width: 0%;
    transition: all 0.2s linear;
  }

  &.scroll-indicator-top {
    top: 0
  }

  &.scroll-indicator-bottom {
    bottom: 0;
  }
}

//preloader
body.wcf-preloader-active {
  overflow: hidden !important;
}

.wcf-preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999;
  transition: all 200ms linear;
  background: #fff;

  //whirlpool
  &.preloader-whirlpool {

    @keyframes cssload-rotate {
      100% {
        transform: rotate(360deg);
      }
    }

    .whirlpool {
      position: absolute;
      top: 50%;
      left: 50%;
      border: 1px solid var(--color2, #CCC);
      border-left-color: var(--color, #000);
      border-radius: 974px;

      &::before,
      &::after {
        position: absolute;
        top: 50%;
        left: 50%;
        border: 1px solid var(--color2, #CCC);
        border-left-color: var(--color, #000);
        border-radius: 974px;
      }

      margin: -24px 0 0 -24px;
      height: 49px;
      width: 49px;
      animation: cssload-rotate 1150ms linear infinite;

      &::before {
        content: "";
        margin: -22px 0 0 -22px;
        height: 43px;
        width: 43px;
        animation: cssload-rotate 1150ms linear infinite;
      }

      &::after {
        content: "";
        margin: -28px 0 0 -28px;
        height: 55px;
        width: 55px;
        animation: cssload-rotate 2300ms linear infinite;
      }
    }
  }

  //floating circle
  &.preloader-floating-circles {
    @keyframes f_fadeG {
      0% {
        background-color: var(--color2, #000);
      }

      100% {
        background-color: rgb(255, 255, 255);
      }
    }

    .floating-circles {
      position: relative;
      width: 80px;
      height: 80px;
      transform: scale(0.6);
    }

    .f_circleG {
      position: absolute;
      background-color: rgb(255, 255, 255);
      height: 14px;
      width: 14px;
      border-radius: 7px;
      animation-name: f_fadeG;
      animation-duration: 0.672s;
      animation-iteration-count: infinite;
      animation-direction: normal;
    }

    #frotateG_01 {
      left: 0;
      top: 32px;
      animation-delay: 0.2495s;
    }

    #frotateG_02 {
      left: 9px;
      top: 9px;
      animation-delay: 0.336s;
    }

    #frotateG_03 {
      left: 32px;
      top: 0;
      animation-delay: 0.4225s;
    }

    #frotateG_04 {
      right: 9px;
      top: 9px;
      animation-delay: 0.509s;
    }

    #frotateG_05 {
      right: 0;
      top: 32px;
      animation-delay: 0.5955s;
    }

    #frotateG_06 {
      right: 9px;
      bottom: 9px;
      animation-delay: 0.672s;
    }

    #frotateG_07 {
      left: 32px;
      bottom: 0;
      animation-delay: 0.7585s;
    }

    #frotateG_08 {
      left: 9px;
      bottom: 9px;
      animation-delay: 0.845s;
    }
  }

  //eight-spinning
  &.preloader-eight-spinning {
    @keyframes cssload-clockwise {
      0% {
        transform: rotate(-45deg);
      }

      100% {
        transform: rotate(315deg);
      }
    }
    @keyframes cssload-not-clockwise {
      0% {
        transform: rotate(45deg);
      }

      100% {
        transform: rotate(-315deg);
      }
    }
    @keyframes cssload-lt {
      0% {
        opacity: 1;
      }

      25% {
        opacity: 1;
      }

      26% {
        opacity: 0;
      }

      75% {
        opacity: 0;
      }

      76% {
        opacity: 1;
      }

      100% {
        opacity: 1;
      }
    }

    .eight-spinning {
      width: 72px;
      margin: 0px auto;
      font-size: 0;
      position: relative;
      transform-origin: 50% 50%;
      animation: cssload-clockwise 6.9s linear infinite;

      &:before {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 39px;
        height: 39px;
        border: 6px solid var(--color2, #E5E5E5FF);
        border-radius: 100%;
        box-sizing: border-box;
      }

      &:after {
        position: absolute;
        content: '';
        z-index: -1;
        top: 0;
        right: 0;
        width: 39px;
        height: 39px;
        border: 6px solid var(--color2, #E5E5E5FF);
        border-radius: 100%;
        box-sizing: border-box;
      }
    }

    .cssload-lt,
    .cssload-rt,
    .cssload-lb,
    .cssload-rb {
      position: relative;
      display: inline-block;
      overflow: hidden;
      width: 39px;
      height: 19px;
      opacity: 1;
    }

    .cssload-lt:before,
    .cssload-rt:before,
    .cssload-lb:before,
    .cssload-rb:before {
      position: absolute;
      content: '';
      width: 39px;
      height: 39px;
      border-top: 6px solid var(--color, #574357FF);
      border-right: 6px solid transparent;
      border-bottom: 6px solid transparent;
      border-left: 6px solid transparent;
      border-radius: 100%;
      box-sizing: border-box;
    }

    .cssload-lt {
      margin-right: -6px;
      animation: cssload-lt 2.3s linear -2300ms infinite;

      &:before {
        top: 0;
        left: 0;
        animation: cssload-not-clockwise 1.15s linear infinite;
      }
    }

    .cssload-rt {
      animation: cssload-lt 2.3s linear -1150ms infinite;

      &:before {
        top: 0;
        right: 0;
        animation: cssload-clockwise 1.15s linear infinite;
      }
    }

    .cssload-lb {
      margin-right: -6px;
      animation: cssload-lt 2.3s linear -1725ms infinite;

      &:before {
        bottom: 0;
        left: 0;
        animation: cssload-not-clockwise 1.15s linear infinite;
      }
    }

    .cssload-rb {
      animation: cssload-lt 2.3s linear -575ms infinite;

      &:before {
        bottom: 0;
        right: 0;
        animation: cssload-clockwise 1.15s linear infinite;
      }
    }
  }

  //double torus
  &.preloader-double-torus {
    @keyframes cssload-spin {
      100% {
        transform: rotate(360deg);
      }
    }

    .double-torus {
      width: 49px;
      height: 49px;
      margin: 0 auto;
      border: 4px double;
      border-radius: 50%;
      border-color: transparent var(--color, #000000E5) var(--color, #000000E5);
      animation: cssload-spin 960ms infinite linear;
    }
  }

  //tube tunnel
  &.preloader-tube-tunnel {
    .tube-tunnel {
      width: 49px;
      height: 49px;
      margin: 0 auto;
      border: 4px solid;
      border-radius: 50%;
      border-color: var(--color, #000);
      animation: cssload-scale 1035ms infinite linear;
    }

    @keyframes cssload-scale {
      0% {
        transform: scale(0);
      }

      90% {
        transform: scale(0.7);
      }

      100% {
        transform: scale(1);
      }
    }
  }

  //speeding wheel
  &.preloader-speeding-wheel {
    @keyframes cssload-spin {
      100% {
        transform: rotate(360deg);
      }
    }

    .speeding-wheel {
      width: 49px;
      height: 49px;
      margin: 0 auto;
      border: 3px solid var(--color, #000);
      border-radius: 50%;
      border-left-color: transparent;
      border-right-color: transparent;
      animation: cssload-spin 575ms infinite linear;
    }
  }

  //loading
  &.preloader-loading {
    @keyframes cssload-loading-ani {
      70% {
        transform: scale(0.5);
      }
    }

    .loading-wrapper {
      display: block;
      margin: 0px auto;
      width: 97px;
    }

    .cssload-loading i {
      width: 49px;
      height: 49px;
      display: inline-block;
      border-radius: 50%;

      &:nth-child(1) {
        background: var(--color, #FF5954FF);
        animation: cssload-loading-ani 1.15s ease-in-out infinite;
      }

      &:nth-child(2) {
        background: var(--color2, #00A8CEFF);
        margin-left: -10px;
        animation: cssload-loading-ani 1.15s ease-in-out 0.58s infinite;
      }
    }
  }

  //dot loading
  &.preloader-dot-loading {
    @keyframes cssload-loading-ani1 {
      100% {
        transform: translate(39px);
        opacity: 0;
      }
    }

    @keyframes cssload-loading-ani2 {
      100% {
        transform: translate(19px);
        opacity: 1;
      }
    }

    @keyframes cssload-loading-ani3 {
      100% {
        transform: translate(19px);
      }
    }

    .dot-loading {
      display: block;
      margin: 0px auto;
      width: 97px;
    }

    .cssload-loading i {
      width: 19px;
      height: 19px;
      display: inline-block;
      border-radius: 50%;
      background: var(--color, #2A2B26FF);

      &:first-child {
        opacity: 0;
        animation: cssload-loading-ani2 0.58s linear infinite;
        transform: translate(-19px);
      }

      &:nth-child(2), &:nth-child(3) {
        animation: cssload-loading-ani3 0.58s linear infinite;
      }

      &:last-child {
        animation: cssload-loading-ani1 0.58s linear infinite;
      }
    }
  }

  //fountainTextG
  &.preloader-fountainTextG {
    @keyframes bounce_fountainTextG {
      0% {
        transform: scale(1);
        color: var(--color, #000000);
      }

      100% {
        transform: scale(0.5);
        color: var(--color2, #ffffff);
      }
    }

    .fountainTextG {
      width: 300px;
      margin: auto;
      display: flex;
      justify-content: center;
    }

    .textG {
      color: var(--color, #000000);
      font-size: 31px;
      text-decoration: none;
      font-weight: normal;
      font-style: normal;
      animation-name: bounce_fountainTextG;
      animation-duration: 2.09s;
      animation-iteration-count: infinite;
      animation-direction: normal;
      transform: scale(0.5);
    }

    #textG_1 {
      animation-delay: 0.75s;
    }

    #textG_2 {
      animation-delay: 0.9s;
    }

    #textG_3 {
      animation-delay: 1.05s;
    }

    #textG_4 {
      animation-delay: 1.2s;
    }

    #textG_5 {
      animation-delay: 1.35s;
    }

    #textG_6 {
      animation-delay: 1.5s;
    }

    #textG_7 {
      animation-delay: 1.64s;
    }
  }

  //circle loading
  &.preloader-circle-loading {
    @keyframes cssload-animation {
      0% {
        transform: scale(0);
      }

      100% {
        transform: scale(1);
      }
    }

    .cssload-loader {
      width: 49px;
      height: 49px;
      border-radius: 50%;
      margin: 0;
      display: inline-block;
      position: relative;
      vertical-align: middle;
      background-color: var(--color, #D3D3D3FF);
      animation: 1.15s infinite ease-in-out;

      &:before, &:after {
        animation: 1.15s infinite ease-in-out;
      }

      &:before, &:after {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        position: absolute;
        top: 0;
        left: 0;
      }

      &:before, &:after {
        content: "";
      }

      &:before {
        content: '';
        border: 10px solid var(--color2, #ffffff);
        top: 0px;
        left: 0px;
        animation-name: cssload-animation;
      }
    }
  }

  //dot circle rotator
  &.preloader-dot-circle-rotator {
    @keyframes cssload-loader {
      0% {
        transform: rotate(0deg);
      }

      50% {
        transform: rotate(180deg);
      }

      100% {
        transform: rotate(180deg);
      }
    }

    .dot-circle-rotator {
      position: relative;
      width: 12px;
      height: 12px;
      border-radius: 12px;
      background-color: var(--color, #000000);
      animation: cssload-loader 1.15s ease-in-out infinite;

      &:before {
        content: "";
        position: absolute;
        background-color: var(--color, #000000);
        top: 0px;
        left: -24px;
        height: 12px;
        width: 12px;
        border-radius: 12px;
      }

      &:after {
        content: "";
        position: absolute;
        background-color: var(--color, #000000);
        top: 0px;
        left: 24px;
        height: 12px;
        width: 12px;
        border-radius: 12px;
      }
    }

  }

  //bubblingG
  &.preloader-bubblingG {
    @keyframes bubblingG {
      0% {
        width: 10px;
        height: 10px;
        background-color: var(--color, #000000);
        transform: translateY(0);
      }

      100% {
        width: 23px;
        height: 23px;
        background-color: var(--color2, #ffffff);
        transform: translateY(-20px);
      }
    }

    .bubblingG {
      text-align: center;
      width: 78px;
      height: 49px;
      margin: auto;
    }

    span {
      display: inline-block;
      vertical-align: middle;
      width: 10px;
      height: 10px;
      margin: 24px auto;
      background-color: var(--color, #000000);
      border-radius: 49px;
      animation: bubblingG 1.5s infinite alternate;
    }

    #bubblingG_1 {
      animation-delay: 0s;
    }

    #bubblingG_2 {
      animation-delay: 0.45s;
    }

    #bubblingG_3 {
      animation-delay: 0.9s;
    }
  }

  //coffee
  &.preloader-coffee {

    @keyframes cssload-steam {
      100% {
        top: -39px;
        background: rgb(238, 238, 238);
        box-shadow: 34px 0px 0 rgb(238, 238, 238), 17.5px -15px 0 rgb(238, 238, 238);
        opacity: 0.25;
      }
    }

    .coffee {
      text-align: left;
      height: 49px;
      width: 39px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      position: absolute;
      left: 50%;
      z-index: 999;
      background: linear-gradient(to bottom left, var(--color, #F7941EFF), var(--color, #C26C07FF));

      &:before {
        position: absolute;
        content: "";
        right: -15px;
        top: 5px;
        height: 24px;
        width: 10px;
        background: transparent;
        border: 5px solid var(--color, #F7941EFF);
        border-left: 5px solid transparent;
        border-bottom: 5px solid var(--color, #C26C07FF);
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        z-index: 1;
      }

      &:after {
        position: absolute;
        content: "";
        width: 5px;
        height: 29px;
        background: rgb(225, 225, 225);
        border-radius: 4px;
        top: -29px;
        box-shadow: 34px 0px 0 rgb(225, 225, 225), 17.5px -15px 0 rgb(225, 225, 225);
        opacity: 1;
        animation: cssload-steam 3.45s ease infinite;
      }
    }
  }

  //orbit loading
  &.preloader-orbit-loading {
    @keyframes cssload-rotate-one {
      0% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
      }

      100% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
      }
    }

    @keyframes cssload-rotate-two {
      0% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
      }

      100% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
      }
    }

    @keyframes cssload-rotate-three {
      0% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
      }

      100% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
      }
    }

    .orbit-loading {
      position: relative;
      width: 62px;
      height: 62px;
      border-radius: 50%;
      perspective: 780px;
    }

    .cssload-inner {
      position: absolute;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      border-radius: 50%;

      &.cssload-one {
        left: 0%;
        top: 0%;
        animation: cssload-rotate-one 1.15s linear infinite;
        border-bottom: 3px solid var(--color, #000000);
      }

      &.cssload-two {
        right: 0%;
        top: 0%;
        animation: cssload-rotate-two 1.15s linear infinite;
        border-right: 3px solid var(--color, #000000);
      }

      &.cssload-three {
        right: 0%;
        bottom: 0%;
        animation: cssload-rotate-three 1.15s linear infinite;
        border-top: 3px solid var(--color, #000000);
      }
    }
  }

  //battery
  &.preloader-battery {
    @keyframes load {
      0% {
        top: 4.5rem;
      }

      70% {
        top: 1.125rem;
      }

      90% {
        top: 0;
      }

      95% {
        top: 0;
      }

      100% {
        top: 4.5rem;
      }
    }

    @keyframes cssload-liquid-1 {
      0% {
        height: 0;
        opacity: 0;
        top: -0.5rem;
      }

      22% {
        height: 0.28125rem;
        top: 0.375rem;
        opacity: 1;
      }

      25% {
        top: -0.25rem;
      }

      35% {
        height: 1.125rem;
        top: -0.5rem;
      }

      55% {
        height: 0.28125rem;
        top: -0.125rem;
      }

      60% {
        height: 0.61875rem;
        opacity: 1;
        top: -0.275rem;
      }

      96% {
        height: 0.84375rem;
        opacity: 0;
        top: 0.5rem;
      }

      100% {
        height: 0;
        opacity: 0;
      }
    }

    @keyframes cssload-liquid-2 {
      0% {
        height: 0;
        opacity: 0;
        top: -0.5rem;
      }

      17.5% {
        height: 0.28125rem;
        top: 0.2rem;
        opacity: 1;
      }

      20% {
        top: -0.25rem;
      }

      25% {
        height: 1.40625rem;
        top: -0.625rem;
      }

      45% {
        height: 0.28125rem;
        top: -0.125rem;
      }

      60% {
        height: 1.40625rem;
        opacity: 1;
        top: -0.5rem;
      }

      96% {
        height: 0.84375rem;
        opacity: 0;
        top: 0.5rem;
      }

      100% {
        height: 0;
        opacity: 0;
      }
    }

    .battery {
      display: block;
      margin: 0px auto;
      position: relative;
      width: 2.25rem;
      height: 4.5rem;
      box-shadow: 0 0 0 0.2rem rgb(66, 92, 119);
      border-radius: 0.09rem;
      background: white;

      &:before {
        content: '';
        position: absolute;
        left: 0.5625rem;
        right: 0.5625rem;
        top: -0.3375rem;
        height: 0.3375rem;
        width: 1.125rem;
        background: rgb(66, 92, 119);
        border-radius: 0.18rem;
      }

      &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        border-right: 2.25rem solid transparent;
        border-bottom: 4.05rem solid rgba(255, 255, 255, 0.32);
      }
    }

    .cssload-liquid {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 2.25rem;
      background: var(--color, #71FB85FF);
      animation: load 2.59s infinite;

      &:after, &:before {
        content: '';
        position: absolute;
        top: -0.5rem;
        height: 1.125rem;
        width: 1.4625rem;
        background: var(--color, #71FB85FF);
        opacity: 0;
        border-radius: 50%;
      }

      &:after {
        right: 0;
        animation: cssload-liquid-1 2.59s infinite;
      }

      &:before {
        left: 0;
        animation: cssload-liquid-2 2.59s infinite;
      }
    }
  }

  //equalizer
  &.preloader-equalizer {
    @keyframes cssload-sequence1 {
      0% {
        height: 10px;
      }

      50% {
        height: 49px;
      }

      100% {
        height: 10px;
      }
    }

    @keyframes cssload-sequence2 {
      0% {
        height: 19px;
      }

      50% {
        height: 63px;
      }

      100% {
        height: 19px;
      }
    }

    .equalizer {
      position: relative;
      margin: 50px auto;
      width: 88px;
    }

    ul {
      margin: 0;
      list-style: none;
      width: 88px;
      position: relative;
      padding: 0;
      height: 10px;

      li {
        position: absolute;
        width: 2px;
        height: 0;
        background-color: var(--color, #000000);
        bottom: 0;
      }
    }

    li {
      &:nth-child(1) {
        left: 0;
        animation: cssload-sequence1 1.15s ease infinite;
      }

      &:nth-child(2) {
        left: 15px;
        animation: cssload-sequence2 1.15s ease infinite 0.12s;
      }

      &:nth-child(3) {
        left: 29px;
        animation: cssload-sequence1 1.15s ease-in-out infinite 0.23s;
      }

      &:nth-child(4) {
        left: 44px;
        animation: cssload-sequence2 1.15s ease-in infinite 0.35s;
      }

      &:nth-child(5) {
        left: 58px;
        animation: cssload-sequence1 1.15s ease-in-out infinite 0.46s;
      }

      &:nth-child(6) {
        left: 73px;
        animation: cssload-sequence2 1.15s ease infinite 0.58s;
      }
    }
  }

  //square-swapping
  &.preloader-square-swapping {
    @keyframes blend-anim {
      0% {
        transform: scale(0.01, 0.01) rotateY(0);
        animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
      }

      50% {
        transform: scale(1, 1) rotateY(0);
        animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
      }

      100% {
        transform: scale(0.01, 0.01) rotateY(0);
      }
    }

    @keyframes cssload-part-anim {
      0% {
        transform: translate3d(-10px, -10px, 0);
      }

      100% {
        transform: translate3d(10px, 10px, 0);
      }
    }

    .square-swapping {
      margin: 30px auto;
      width: 19px;
      height: 19px;
      transform: rotate(-45deg);
    }

    .cssload-square-part {
      position: absolute;
      width: 19px;
      height: 19px;
      z-index: 1;
      animation: cssload-part-anim 0.92s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite alternate;
    }

    .cssload-square-green {
      background: var(--color, #54FAD4FF);
      right: 0;
      bottom: 0;
      animation-direction: alternate-reverse;
    }

    .cssload-square-pink {
      background: var(--color2, #E96F92FF);
      left: 0;
      top: 0;
    }

    .cssload-square-blend {
      background: rgba(0, 0, 0, 0.5);
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 2;
      animation: blend-anim 0.92s ease-in infinite;
    }
  }

  //jackhammer
  &.preloader-jackhammer {

    @keyframes cssload-upDown {
      from {
        transform: translateY(19px);
      }

      to {
        transform: translateY(-19px);
      }
    }

    @keyframes cssload-upDown2 {
      from {
        transform: translateY(29px);
      }

      to {
        transform: translateY(-19px);
      }
    }

    * {
      box-sizing: border-box;
    }

    .jackhammer {
      margin: 0 auto;
      max-width: 545px;
    }

    ul li {
      list-style: none;
    }

    .cssload-flex-container {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;

      li {
        padding: 10px;
        height: 97px;
        width: 97px;
        margin: 0;
        position: relative;
        text-align: center;
      }
    }

    .cssload-loading {
      display: inline-block;
      position: relative;
      width: 5px;
      height: 49px;
      background: var(--color, #000000);
      margin-top: 5px;
      border-radius: 975px;
      animation: cssload-upDown2 1.15s ease infinite;
      animation-direction: alternate;
      animation-delay: 0.29s;

      &:after,
      &:before {
        display: inline-block;
        position: relative;
        width: 5px;
        height: 49px;
        background: var(--color, #000000);
        margin-top: 5px;
        border-radius: 975px;
        animation: cssload-upDown2 1.15s ease infinite;
        animation-direction: alternate;
        animation-delay: 0.29s;
      }

      &:after {
        position: absolute;
        content: '';
        animation: cssload-upDown 1.15s ease infinite;
        animation-direction: alternate;
      }

      &:before {
        position: absolute;
        content: '';
        animation: cssload-upDown 1.15s ease infinite;
        animation-direction: alternate;
        left: -10px;
      }

      &:after {
        left: 10px;
        animation-delay: 0.58s;
      }
    }
  }
}

//advance tooltips
.wcf-advanced-tooltip {
  position: absolute;
  z-index: 999;
  display: none;
  padding: 5px 0;
  width: 120px;
  height: auto;
  border-radius: 6px;
  background-color: #000;
  color: #fff;
  text-align: center;
  opacity: 0;
  --tooltip-arrow-color: black;
  --tooltip-arrow-distance: 0px;
}

.wcf-advanced-tooltip::after {
  position: absolute;
  border-width: 5px;
  border-style: solid;
  content: ""
}

.wcf-advanced-tooltip.no-arrow::after {
  visibility: hidden
}

.wcf-advanced-tooltip.show {
  display: inline-block;
  opacity: 1
}

.wcf-advanced-tooltip-top .wcf-advanced-tooltip,
body[data-elementor-device-mode=tablet] .wcf-advanced-tooltip-tablet-top .wcf-advanced-tooltip {
  top: unset;
  right: 0;
