.wcf--popup-video svg {
  width: 1em;
  height: 1em;
}

#wcf-aae-global--popup-js,
.wcf--popup-video-wrapper {
  position: fixed;
  left: -1px;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(11, 11, 11, 0.9);
  transform: scaleY(0) translateX(-100%);
  opacity: 0;
  visibility: hidden;
  transform-origin: center;
  z-index: 5;
}

.wcf--popup-video {
  position: relative;
  transform: scaleY(0);
  opacity: 0;
  visibility: hidden;
  max-width: 900px;
  width: 100%;
  height: 520px;
}

.aae-popup-content-container {
  height: 100%;
  width: 100%;

  .aae-post-gallery-wrapper {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .aae-gallery-btn {
    position: absolute;
    font-size: 25px;
    z-index: 9999;
    top: 50%;
    transform: translateY(-50%);

    &.btn-next {
      right: 0;
    }
  }
}

.wcf--popup-video iframe {
  height: 100%;
  border: 1px solid #222;
  border-radius: 3px;
}

.aae-popup-content {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;

  li {
    list-style: inherit;
  }
}

.audio.wcf-audio-wrapper-clean {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  audio {
    width: 100%;
  }
}

.wcf--popup-close {
  position: absolute;
  right: -40px;
  top: -40px;
  border: 1px solid #aaa;
  width: 44px;
  height: 44px;
  border-radius: 100%;
  color: #fff;
  fill: #fff;
  line-height: 0;
  opacity: 0.8;
  transition: all 0.3s;
}

.wcf--popup-close:hover {
  opacity: 1;
}

.wcf--popup-video p {
  color: #fff;
  font-size: 20px;
}

.wcf__posts-slider,
.wcf__banner-posts,
.wcf__feature-posts,
.wcf-posts {
  .aae-post-gallery-wrapper {
    position: absolute;
    width: 0;
    height: 0;
  }
}

@media (max-width: 991px) {
  .wcf--popup-video {
    width: 96%;
    height: 480px;
  }
  .wcf--popup-close {
    right: 0;
    top: -50px;
  }

}

@media (max-width: 767px) {
  .wcf--popup-video {
    height: 360px;
  }

}