:root {
  scroll-behavior: auto !important;
}

.wcf-hover-cursor-effect {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  top: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  background: #000;
  color: #fff;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  z-index: 999;
}

.wcf-image-hover {
  transition: opacity 0.3s, transform 0.7s cubic-bezier(0.23, 1, 0.32, 1);
  position: absolute;
  width: 300px;
  height: 400px;
  object-fit: cover;
  left: 0;
  top: -50%;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  pointer-events: none;
}

[data-wcf-wrapper-link] {
  cursor: pointer;
}

.wcf-cursor {
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  border: 1px solid #000;
  z-index: 999;
  transition: 0.15s;
}

.wcf-cursor-follower {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  pointer-events: none;
  width: 8px;
  height: 8px;
  background-color: #000;
  border-radius: 50%;
  transition: 0.2s;
  z-index: 999;
}

.wcf-scroll-to-top {
  position: fixed;
  right: 30px;
  bottom: 30px;
  height: 46px;
  width: 46px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  transform: translateY(15px);
  transition: all 200ms linear;
}
.wcf-scroll-to-top svg {
  width: 1em;
  height: 1em;
}
.wcf-scroll-to-top.show-scroll-to-top {
  opacity: 1;
  visibility: visible;
  transform: translateY(0px);
}
.wcf-scroll-to-top.scroll-to-circle {
  border-radius: 100%;
}
.wcf-scroll-to-top svg.progress-circle {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
.wcf-scroll-to-top svg.progress-circle path {
  fill: none;
  stroke: #1f2029;
  stroke-width: 4;
  box-sizing: border-box;
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}

.wcf-scroll-indicator {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 8px;
  background: #ccc;
}
.wcf-scroll-indicator .indicator-bar {
  height: 100%;
  background: #000000;
  width: 0%;
  transition: all 0.2s linear;
}
.wcf-scroll-indicator.scroll-indicator-top {
  top: 0;
}
.wcf-scroll-indicator.scroll-indicator-bottom {
  bottom: 0;
}

body.wcf-preloader-active {
  overflow: hidden !important;
}

.wcf-preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999;
  transition: all 200ms linear;
  background: #fff;
}
